<template>
    <div class="messages-container">
      <el-dialog
        :visible.sync="deleteConfirmVisible"
        title=""
        width="30%"
        :show-close="false"
      >
        <span></span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="deleteConfirmVisible = false"> </el-button>
          <el-button type="danger" @click="confirmDeleteRead"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <div class="actions">
        <el-button type="primary" @click="markAllAsRead" class="custom-btn"></el-button>
        <el-button type="danger" @click="deleteReadMessages" class="custom-btn"></el-button>
      </div>
  
      <!--  -->
      <div v-if="messages.length === 0" class="no-messages"></div>
  
      <!--  -->
      <div class="message-card-container">
        <div class="message-card" v-for="msg in sortedMessages" :key="msg.notfId" @click="handleRead(msg)">
          <div class="message-left">
            <p class="message-content" v-html="formatContent(msg.content)"></p>
            <p class="message-time">{{ formatTime(msg.createTime) }}</p>
          </div>
          <div class="message-right">
            <el-tag v-if="msg.status === 'New'" type="danger"></el-tag>
            <el-tag v-else type="success"></el-tag>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import axios from "axios";
  
  export default {
    data() {
      return {
        token: localStorage.getItem("token"),
        messages: [],
        deleteConfirmVisible: false,  
        unread:0,
      };
    },
    computed: {
      sortedMessages() {
        return this.messages
          .filter((msg) => msg.status !== "Deleted")
          .sort((a, b) => {
            if (a.status !== b.status) {
              return a.status === "New" ? -1 : 1; // 
            }
            return new Date(b.createTime) - new Date(a.createTime); // 
          });
      },
    },
    mounted() {
      this.fetchMessages();
      //this.count();
    },
    methods: {
        count(){
          this.$axios
          .get("/user/notification", {
            headers: { token: this.token },
            params:{userId:localStorage.getItem('userId')},
          })
          .then((res) => {
            if (res.data.code === "1") {
              this.unread = res.data.data;
            }
          });
        },
      // 
      deleteReadMessages() {
        const readCount = this.messages.filter(m => m.status === "Read").length;
        if (readCount === 0) {
          this.$message.warning("");
          return;
        }
        this.deleteConfirmVisible = true;
      },
  
      // 
      confirmDeleteRead() {
        const readMsgs = this.messages.filter((m) => m.status === "Read");
        const requests = readMsgs.map((msg) =>
          this.$axios.delete("/user/notification", {
            params: { notfId: msg.notfId },
            headers: { token: this.token },
          })
        );
  
        Promise.all(requests)
          .then(() => {
            this.messages = this.messages.filter((m) => m.status !== "Read");
            this.$message.success(`${readMsgs.length}`);
          })
          .catch(() => {
            this.$message.error("");
          })
          .finally(() => {
            this.deleteConfirmVisible = false;
          });
      },
      fetchMessages() {
        this.$axios
          .get("/user/notification/list", {
            headers: { token: this.token },
            params:{userId:localStorage.getItem('userId')},
          })
          .then((res) => {
            if (res.data.code === "1") {
              this.messages = res.data.data;
            }
          });
      },
      formatTime(datetime) {
        const date = new Date(datetime);
        const y = date.getFullYear();
        const m = date.getMonth() + 1;
        const d = date.getDate();
        const h = date.getHours().toString().padStart(2, "0");
        const min = date.getMinutes().toString().padStart(2, "0");
        return `${y}-${m}-${d} ${h}:${min}`;
      },
      formatContent(content) {
        if (!content) return '';
        return content
          .split(",")
          .map((str) => str.trim())
          .join("<br>");
      },
      handleRead(msg) {
        if (msg.status === "New") {
          this.$axios
            .put(
              "/user/notification",
              {},
              {
                params: { notfId: msg.notfId },
                headers: { token: this.token },
              }
            )
            .then((res) => {
              if (res.data.code === "1") {
                this.$set(msg, 'status', 'Read'); //  $set 
                this.messages = [...this.messages]; // 
              }
            });
        }
      },
  
  
      markAllAsRead() {
        const unread = this.messages.filter((m) => m.status === "New");
        const requests = unread.map((msg) =>
          this.$axios.put(
            "/user/notification",
            {},
            {
              params: { notfId: msg.notfId },
              headers: { token: this.token },
            }
          )
        );
  
        Promise.all(requests).then(() => {
          this.messages.forEach((msg) => {
            if (msg.status === "New") msg.status = "Read";
          });
        });
      },
    },
  };
  </script>
  
  <style scoped>
  .messages-container {
    padding: 20px;
    height: calc(100vh - 40px); /* padding */
    overflow-y: auto;
    box-sizing: border-box;
    background-color: white;
    text-align: left;
    width: 100%;
    bottom: 20px;
  }
  
  .message-card-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /*  */
    gap: 24px; /*  */
    grid-auto-rows: minmax(150px, auto);
    overflow-y: auto;
    height: calc(100vh - 140px); /*  */
    padding-bottom: 20px; /*  */
  }
  
  .actions {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 20px;
    gap: 12px;
  }
  
  .custom-btn {
    background-color: #ffe4b5;
    color: #8b4513;
    border: 1px solid #ffe4b5;
  }
  .message-card {
    display: flex;
    flex-direction: column; /*  */
    justify-content: space-between; /*  */
    align-items: flex-start;
    background-color: white;
    border: 1px solid #FFE4B5;
    border-radius: 12px;
    padding: 16px;
    transition: background-color 0.2s ease-in-out;
    cursor: pointer;
    height: 170px; /*  */
    overflow: hidden; /*  */
  }
  
  .message-left {
    flex: 1;
    margin-right: 12px;
    display: flex;
    flex-direction: column;
    justify-content: space-between; /*  */
  }
  
  .message-content {
    font-size: 16px;
    color: #333;
    margin-bottom: 6px;
    white-space: pre-wrap;
    line-height: 1.5;
    overflow-y: auto; /*  */
    max-height: 100px; /*  */
  }
  
  .message-time {
    font-size: 14px;
    color: #999;
    margin-top: auto;
  }
  
  .message-right {
    display: flex;
    align-items: center;
    gap: 8px;
    padding-top: 12px; /*  */
  }
  
  .no-messages {
    text-align: center;
    font-size: 20px;
    color: #999;
    margin-top: 100px;
  }
  
  .message-card:hover {
    background-color: #f4e7d3;
  }
  
  
  
  .no-messages {
    text-align: center;
    font-size: 20px;
    color: #999;
    margin-top: 100px;
  }
  .dialog-footer {
    text-align: right;
  }
  
  
  .el-button--primary:hover,
  .el-button--danger:hover {
    background-color: #ffd699;
  }
  /*  */
  
  </style>
  